import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import "../css/employeecard.css";

class Employeecard extends React.Component {
  selecteemployee(employee) {
    this.props.dispatch({
      selectedemployee: employee,
      type: "selectedemployee",
    });
    // this.props.history.push("/employeedetails");
  }
  displayemployeelist() {
    console.log(this.props.employeeArray);
    return this.props.employeeArray.map((employee) => {
      return (
        <div className="employeecard">
          <img src={"./img/dummy.jpg"} alt="" />
          <h2>{employee.Name}</h2>
          <p>1</p>
          {/* <button>view details</button> */}
          <Link
            onClick={() => this.selecteemployee(employee)}
            to={"/employeedetails"}
          >
            view details
          </Link>
        </div>
      );
    });
  }
  render() {
    return <div>{this.displayemployeelist()}</div>;
  }
}

function mapStateToProps(state) {
  return {
    employeeArray: state.employeeArray,
  };
}

export default connect(mapStateToProps)(Employeecard);

