import React from "react";
import "../css/table.css";

class Table extends React.Component {
  data = [];
  originalData = [];
  // pageLimit = 10;
  // page = 2;
  state = {
    pageLimit: 10,
    page: 1,
  };
  constructor(props) {
    super(props);
    this.originalData = this.props.data.slice();
    // this.filterData();
    console.log(this.props);
  }
  filterData() {
    let start = (this.state.page - 1) * this.state.pageLimit;
    let end = start + this.state.pageLimit;
    this.data = this.originalData.slice(start, end);
  }

  generateHeadRow(data) {
    // ['name', 'age']
    return data.map((heading) => <th key={"head" + heading}>{heading}</th>);
  }

  generateHeading() {
    let headRow = this.generateHeadRow(Object.keys(this.data[0]));
    return (
      <thead>
        <tr>{headRow}</tr>
      </thead>
    );
  }
  generateEachRow(data) {
    // {      name: "gokul",      age: 22,    }
    let objectKeys = Object.keys(data);
    return objectKeys.map((eachKey) => <td>{data[eachKey]}</td>);
  }
  generateBody() {
    let dataRows = this.data.map((eachElement) => (
      <tr>{this.generateEachRow(eachElement)}</tr>
    ));
    return <tbody>{dataRows}</tbody>;
  }
  render() {
    this.filterData();

    let tableHeading = this.generateHeading();
    let tableBody = this.generateBody();
    return (
      <div>
        <div>
          <button onClick={() => this.setState({ page: 1 })}>1</button>
          <button onClick={() => this.setState({ page: 2 })}>2</button>
          <button onClick={() => this.setState({ page: 3 })}>3</button>
          <button onClick={() => this.setState({ page: 4 })}>4</button>
        </div>
        <table>
          {tableHeading}
          {tableBody}
        </table>
      </div>
    );
  }
}
export default Table;
