import React from "react";
import "../css/calculator.css";

class Calculator extends React.Component {
  constructor() {
    super();
  }

  calcKeys = [1, 2, 3, 4, 5, 6, 7, 8, 9, 0, ".", "%", "+", "-", "*", "/", "="];
  alert() {
    this.alert("hello");
  }

  display(buttonText) {
    // alert(buttonText);
    if (buttonText != "=") {
      var outputscreen = document.getElementById("output-screen");
      outputscreen.value += buttonText;
    } else {
      this.calculate();
    }
  }

  calculate() {
    var outputscreen = document.getElementById("output-screen");

    try {
      outputscreen.value = eval(outputscreen.value);
    } catch (err) {
      alert("invalid");
    }
  }

  generateButton(buttonText) {
    return (
      <button onClick={() => this.display(buttonText)}>{buttonText}</button>
    );
  }
  render() {
    let displayKeys = this.calcKeys.map((each) => this.generateButton(each));
    return (
      <div className="container">
        <div className="calculator">
          <input type="text" placeholder="0" id="output-screen" value="" />
          {displayKeys}
          {/* <but
          on onClick="clear()">cl</button>
          <button onClick="del()">DEL</button> */}
          {/* <button onClick={() => this.display()}>%</button> */}
          {/* <button onClick="display('/')">/</button>
          <button onClick="display('7')">7</button>
          <button onClick="display('8')">8</button>
          <button onClick="display('9')">9</button>
          <button onClick="display('*')">*</button>
          <button onClick="display('4')">4</button>
          <button onClick="display('5')">5</button>
          <button onClick="display('6')">6</button>
          <button onClick="display('-')">-</button>
          <button onClick="display('1')">1</button>
          <button onClick="display('2')">2</button>
          <button onClick="display('3')">3</button>
          <button onClick="display('+')">+</button>
          <button onClick="display('0')">0</button>
          <button onClick="display('.')">.</button>
          <button onClick="calculate()">=</button> */}
        </div>
      </div>
    );
  }
}

export default Calculator;
